@import url(https://fonts.googleapis.com/css?family=Chilanka|Oswald&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background: -webkit-linear-gradient(45deg, red, blue);
  background: linear-gradient(45deg, red, blue); }

.wrap {
  min-height: 100vh; }
  .wrap h1 {
    font-family: 'Chilanka', cursive;
    color: #cdc0c0;
    text-align: center;
    padding-top: 20px; }
  .wrap label {
    font-size: 23px;
    color: white; }
  .wrap input#important {
    width: 20px;
    height: 20px; }
  .wrap .task-list {
    background-color: #9932cc87;
    border: 2px solid white;
    color: #cdc0c0;
    padding: 10px;
    margin: 5px; }
    .wrap .task-list .single-task {
      -webkit-animation: task .1s .2s both;
              animation: task .1s .2s both; }
    .wrap .task-list .one-task,
    .wrap .task-list .free-time {
      font-size: 20px; }
    .wrap .task-list .task-done,
    .wrap .task-list .delete-task {
      font-size: 20px;
      padding: 5px;
      background: none;
      border: none;
      color: #cdc0c0; }
  .wrap .list {
    min-width: 50%;
    min-height: 50%;
    font-family: 'Oswald', Arial, sans-serif; }
    .wrap .list .wrapNewTask {
      max-width: 310px;
      margin: 10px auto; }
      .wrap .list .wrapNewTask .add {
        display: inline-block;
        color: #cdc0c0;
        margin-left: 10px;
        font-size: 25px; }
    .wrap .list .newTask {
      font-size: 20px; }

@-webkit-keyframes task {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes task {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@media (min-width: 410px) and (orientation: portrait) {
  .wrap .list .newTask {
    font-size: 21px; } }

@media (min-width: 765px) {
  .wrap .list .newTask {
    font-size: 30px; }
  .wrap .list .wrapNewTask {
    max-width: 430px; }
    .wrap .list .wrapNewTask .add {
      font-size: 35px; } }

@media (min-width: 1023px) {
  .wrap {
    max-width: 80vw;
    margin: 0 auto; } }

